
<template>
    <div class="padding">
    <mdb-jumbotron style="background-image: url(http://25.media.tumblr.com/tumblr_mdyju172z11qahp72o9_1280.jpg);background-size:cover;background-position:top center;width:100%;" class="mb-0 p-0">

    <mdb-card-body class="text-center">
      <h3 class="card-title h3 my-4" style="text-align:left;color:black;position:bottom;"><strong>HAIRSPIRATION</strong></h3>
    </mdb-card-body>
  </mdb-jumbotron>

  <mdb-btn color="elegant" class="buttons" v-for="entry in filterList" :item="entry" :key="entry.id" @click="filter = entry; active = entry.id;" :class="{ active: entry == filter }">{{ entry }}</mdb-btn>
    <mdb-tooltip :options="{placement: 'right'}">
    <span slot="tip"><h1>Tooltip on right</h1><br>entry.name</span>
    <div v-for="entry in images" :key="entry.id">
         <div
           v-if="entry[fkey] === filter || filter === 'All'"
           :item="entry"
           :key="entry.id"
           class="user"
           slot="reference"
         >
         <mdb-card class="card" v-bind:style='{ backgroundImage: "url(" + entry.image + ")",backgroundSize: "cover" }'>
         <mdb-tooltip trigger="hover" :options="{placement: 'top'}">
         <span slot="tip"> {{entry.tip}}</span>
         <a slot="reference"><i class="fa fa-info-circle"></i></a>
           </mdb-tooltip>
         </mdb-card>
         </div>
         </div>
      </mdb-tooltip>
  </div>
</template>

<script>
import axios from 'axios'
import { mdbJumbotron, mdbCard, mdbBtn, mdbTooltip, mdbCardBody } from 'mdbvue'

export default {
  name: 'Braids',
  components: { mdbJumbotron, mdbCard, mdbBtn, mdbTooltip, mdbCardBody },
  data: function () {
    return {
      fkey: 'name',
      filterList: ['Natural', 'Short Hair', 'Lines', 'Box Braids', 'Dreadlocks', 'Wire'],
      filter: 'All',
      images: []
    }
  },
  created () {
    var apiURL = 'https://next.json-generator.com/api/json/get/4JCnNiTCr'
    fetch(apiURL)
      .then(res => res.json())
      .then(res => (this.users = res))
      .catch(error => console.log(error))
  },
  mounted () {
    axios.get('https://hairspiration-api.herokuapp.com/api/v1/hairstyle/').then(response => { this.images = response.data })
  }
}

</script>
